.navWrap {
  padding: 0 20px;
}

.navLink {
  text-decoration: none;
  color: white;
  background-color: black;
  padding: 10px 15px;
  margin-right: 15px;
}

.navLink:hover {
  font-style: italic;
}

.goBackBtn {
  padding: 4px 11px;
  margin: 20px 20px 0 20px;
  display: flex;
  border: 3px solid black;
  border-radius: 6px;
  align-items: center;
}

.goBackBtn:hover {
  font-style: italic;
  cursor: pointer;
}

.btnText {
  font-size: 18px;
  padding-left: 10px;
  text-decoration: none;
}

.arrow {
  max-height: 33px;
}

.buttonWrap {
  display: flex;
  align-items: center;
}
